@import url("https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@500;700;900&display=swap");
html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
}

body {
  font-family: "Red Hat Display", sans-serif;
  font-size: 1rem;
  font-weight: 500;
}

h1, h2, h3 {
  margin-top: 0;
  line-height: 1.1;
}

p {
  margin-top: 0;
}

:root {
  --white: hsl(0, 0%, 100%);
  --light-gray: hsl(0, 0%, 96%);
  --text-gray: hsl(0, 0%, 62%);
  --pale-blue: hsl(225, 100%, 94%);
  --bright-blue: hsl(245, 75%, 52%);
  --very-pale-blue: hsl(225, 100%, 98%);
  --desaturated-blue: hsl(224, 23%, 55%);
  --dark-blue: hsl(223, 47%, 23%);
}

.card {
  width: 21.875rem;
  background-color: var(--white);
  border-radius: 0.5rem;
  overflow: hidden;
  text-align: center;
  z-index: 3;
}
.card__image {
  width: 100%;
}
.card__content {
  padding: 1rem;
}
.card__title {
  font-size: 1.5rem;
  text-align: center;
  font-weight: 900;
}
.card__discription {
  font-size: 0.875rem;
  line-height: 1.5;
  text-align: center;
  color: var(--text-gray);
}
.card__plan {
  background-color: var(--very-pale-blue);
  display: flex;
  align-items: center;
  justify-content: space-around;
  gap: 0.625rem;
  border-radius: 0.5rem;
  padding: 1rem 0.5rem;
  margin-bottom: 0.875rem;
}
.card__plan-price p {
  margin: 0;
}
.card__plan-price--title {
  margin-bottom: 0.5rem;
  font-weight: 900;
}
.card__plan-price--amount {
  color: var(--text-gray);
}
.card__payment-btn {
  display: block;
  margin: 0 auto;
  margin-bottom: 1.375rem;
  width: 100%;
  border-radius: 0.5rem;
  background-color: var(--bright-blue);
  border: none;
}
.card__cancel-order-btn {
  font-size: 0.875rem;
  text-decoration: none;
  margin-bottom: 1.5rem;
  font-weight: 700;
  color: var(--text-gray);
}

.btn {
  color: var(--white);
  padding: 1rem 2rem;
}

body {
  height: 100vh;
  width: 100vw;
  position: relative;
  background-color: var(--pale-blue);
  overflow: hidden;
}

.body-background {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background: url("/src/images/pattern-background-mobile.svg");
  background-repeat: no-repeat;
  background-size: 100%;
}
@media (min-width: 800px) {
  .body-background {
    background: url("/src/images/pattern-background-desktop.svg");
    background-repeat: no-repeat;
    background-size: 100%;
  }
}

.card__container {
  max-width: 1140px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}